	module.exports = {"data":{"site":{"meta":{"title":"Trevor Richardson","description":"Thoughts on life & crafting software","url":"https://trevorrichardson.me","author":"richardson-trevor","twitter":"IntelXDesign"}},"post":{"id":"/Users/trevorr/Development/Home/website/src/pages/articles/2018-11-08-til-center-code-in-jet-brains-editors/index.md absPath of file >>> MarkdownRemark","html":"<p>Today I learned it’s possible to center code in JetBrains products. This is apparently part of what they use to support the distraction-free mode. If you don’t want to completely get rid of tool Windows, etc. but still want to center the code, you can use <a href=\"https://stackoverflow.com/questions/44796433/centre-code-in-android-studio-intellij-idea-editor/44802228#answer-44802228\">this Stack Overflow post</a>.</p>\n<p>Essentially, it comes down to going to <code>Help -> Edit Custom VM Options</code> then enter <code>-Deditor.distraction.free.mode=true</code>.</p>\n<p>Craft on.</p>","frontmatter":{"layout":"post","title":"TIL: Center code in JetBrains editors","path":"/articles/til-center-code-in-jet-brains-editors","categories":["TIL"],"date":"2018/11/08"}}},"pathContext":{"path":"/articles/til-center-code-in-jet-brains-editors"}}

